//Dependencies
import './App.css';

//Components
import React from 'react';
import Body from './components/body/Body';
import Nav from './components/nav/Nav';

function App() {
  //State
  const [hskLevel, setHskLevel] = React.useState(0);

  //Functions
  function toggleHskLevel(num) {
    setHskLevel(num);
  }

  return (
    <div className="App">
      <Nav value={hskLevel} handleChange={(e) => toggleHskLevel(e)} />
      <Body hskLevel={hskLevel} />
    </div>
  );
}

export default App;
