import React from 'react';
import './body.css';
import Data from '../../data.js';

//Components
import Garden from './components/garden/Garden';
import WordDisplayContainer from './components/wordDisplayContainer/WordDisplayContainer';

const Body = ({ hskLevel }) => {
  //State
  const [knownWords, setKnownWords] = React.useState([]);
  const [unknownWords, setUnknownWords] = React.useState([]);
  const [displayRandomWord, setDisplayRandomWord] = React.useState({});
  const [streak, setStreak] = React.useState(0);
  //Create a manipulatable word list
  const userSpecificWordList = Data[hskLevel];

  //Display random word after hsk level changes
  React.useEffect(() => {
    if (hskLevel > 0) {
      const randomNumber = Math.floor(
        Math.random() * userSpecificWordList.length
      );

      console.log(userSpecificWordList[randomNumber].Hanzi);
      setDisplayRandomWord(userSpecificWordList[randomNumber]);
    }
  }, [hskLevel]);

  //Functions

  //Display next word and add known words to knownWords array
  function addKnownWords() {
    displayNextWord();
    const plantsArray = ['🌱', '🌿', '🎋', '🪴', '🌾', '🌵', '🪴', '🍀'];
    const randomNumber = Math.floor(Math.random() * plantsArray.length);

    setStreak((p) => p + 1);
    setKnownWords((p) => [
      ...p,
      [plantsArray[randomNumber], displayRandomWord.Hanzi],
    ]);
  }

  function addUnknownWords() {
    displayNextWord();

    setStreak(0);

    setUnknownWords((p) => [
      ...p,
      [
        displayRandomWord.Hanzi,
        displayRandomWord.Pinyin,
        displayRandomWord.Definitions,
      ],
    ]);
  }

  //Get and set state with random word
  function displayNextWord() {
    if (hskLevel > 0) {
      const randomNumber = Math.floor(
        Math.random() * userSpecificWordList.length
      );
      setDisplayRandomWord(userSpecificWordList[randomNumber]);
    }
  }

  return (
    <>
      <main>
        {hskLevel > 0 ? (
          <WordDisplayContainer
            hskLevel={hskLevel}
            displayRandomWord={displayRandomWord}
            displayNextWord={displayNextWord}
            addKnownWords={addKnownWords}
            addUnknownWords={addUnknownWords}
          />
        ) : (
          <p className="selectLevel">Select your level</p>
        )}
      </main>
      <Garden
        knownWords={knownWords}
        unknownWords={unknownWords}
        streak={streak}
      />
    </>
  );
};

export default Body;
