import React from 'react';
import './garden.css';
const Garden = (props) => {
  //Download function
  const downloadKnownWordsFile = () => {
    const array = props.knownWords
      .map((e, i) => `${i + 1}. ${e[1]} \n`)
      .join('');
    const element = document.createElement('a');
    const file = new Blob([array], {
      type: 'text/csv;charset=utf-8',
    });
    element.href = URL.createObjectURL(file);
    element.download = 'myPlants.csv';
    document.body.appendChild(element); // Required for this to work in FireFox
    element.click();
  };

  const downloadUnknownWordsFile = () => {
    const array = props.unknownWords
      .map((e, i) => `${i + 1}. ${e[0]} / ${e[1]} / ${e[2]}  \n`)
      .join('');
    const element = document.createElement('a');
    const file = new Blob([array], {
      type: 'text/csv;charset=utf-8',
    });
    element.href = URL.createObjectURL(file);
    element.download = 'myPlants.csv';
    document.body.appendChild(element); // Required for this to work in FireFox
    element.click();
  };

  //Generate plants
  const plant = props.knownWords.map((e) => (
    <div key={e} className="plant--pair">
      <div>
        <img
          src={require('../../../../images/plant.png')}
          alt=""
          className="plant--pair--image"
        />
      </div>

      <div className="plant--pair--word">{e[1]}</div>
    </div>
  ));

  //Generate seeds
  const seed = props.unknownWords.map((e) => (
    <div key={e} className="seed--pair">
      <div>
        <img
          src={require('../../../../images/seed.png')}
          alt="seed"
          className="seed--pair--image"
        />
      </div>
      <div className="seed--pair--word">{e[0]}</div>
      <div className="seed--pair--detail">
        <div className="seed--pair--pinyin">{e[1]}</div>
        <div className="seed--pair--translation">{e[2]}</div>
      </div>
    </div>
  ));

  return (
    <section className="garden">
      <div className="known--words--container">
        <div className="garden--knownWords--head">
          <div className="garden--knownWords--title">
            Garden ({props.knownWords.length}){' '}
            {props.streak > 1 && `${props.streak}🔥`}
          </div>
          <div
            className="garden--download--text"
            onClick={downloadKnownWordsFile}
          >
            Download
          </div>
        </div>
        <div className="plant--container">{plant}</div>
      </div>

      <div className="unknown--words--container">
        <div className="garden--unknownWords--head">
          <div className="garden--unknownWords--title">
            Seeds ({props.unknownWords.length})
          </div>
          <div
            className="garden--download--text"
            onClick={downloadUnknownWordsFile}
          >
            Download
          </div>
        </div>
        <div className="seed--container">{seed}</div>
      </div>
    </section>
  );
};

export default Garden;
